import React from "react";
import styles from "./Excerpt.module.scss";

const Excerpt = ({ product, handleClose }) => {
  const { APText, APIcons } = product;
  
  return (
    <div className={styles.excerpt}>
      <div className={styles.excerptLeft}>
        <img src={process.env.PUBLIC_URL + `/${APIcons}`} alt="Product Type Icon" className={styles.appleLogo} />
        <p>{APText}</p>
      </div>
      <button onClick={handleClose} type="button" className={styles.close} data-dismiss="modal">
        <img src={process.env.PUBLIC_URL + `/images/close.svg`} className={styles.closeBtn} alt="Close Button" />
      </button>
    </div>
  );
};

export { Excerpt };
